.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
}

.ps__rail-y {
    right: 0 !important;
    left: auto !important;
}

#jqstooltip {
    width: auto !important;
    height: auto !important;
    padding: 5px 10px !important;
    border-radius: 2px !important;
}

table.dataTable.no-footer {
    border-bottom: 1px solid rgba(0, 0, 0, 0.0625);
    margin-bottom: 20px;
}

.sorting_asc:focus {
    outline: none;
}

.dataTables_wrapper {
    overflow: hidden;
    padding-bottom: 5px;
}

.dataTables_wrapper .dataTables_length {
    color: #313435;
    float: left;
}

@media screen and (max-width: 767px) {
    .dataTables_wrapper .dataTables_length {
        text-align: left;
    }
}

.dataTables_wrapper .dataTables_length select {
    border: 1px solid rgba(0, 0, 0, 0.0625);
    border-radius: 2px;
    -webkit-box-shadow: none;
    box-shadow: none;
    height: 35px;
    font-size: 14px;
    padding: 5px;
    margin-left: 5px;
    margin-right: 5px;
    color: #72777a;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.dataTables_wrapper .dataTables_filter {
    color: #313435;
    float: right;
}

@media screen and (max-width: 767px) {
    .dataTables_wrapper .dataTables_filter {
        text-align: left;
    }
}

.dataTables_wrapper .dataTables_filter input {
    border: 1px solid rgba(0, 0, 0, 0.0625);
    border-radius: 2px;
    -webkit-box-shadow: none;
    box-shadow: none;
    height: 35px;
    font-size: 14px;
    margin-left: 15px;
    padding: 5px;
    color: #72777a;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.dataTables_wrapper .dataTables_info {
    color: #72777a;
    float: left;
}

.dataTables_wrapper .dataTables_processing {
    color: #313435;
}

.dataTables_wrapper .dataTables_paginate {
    color: #72777a;
    float: right;
}

.dataTables_wrapper .dataTables_paginate .paginate_button {
    color: #72777a !important;
    padding: 6px 12px;
    border-radius: 2px;
    margin-right: 10px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.dataTables_wrapper .dataTables_paginate .paginate_button.next,
.dataTables_wrapper .dataTables_paginate .paginate_button.previous,
.dataTables_wrapper .dataTables_paginate .paginate_button.first,
.dataTables_wrapper .dataTables_paginate .paginate_button.last {
    border-radius: 2px;
    text-decoration: none;
}

.dataTables_wrapper .dataTables_paginate .paginate_button.next:hover,
.dataTables_wrapper .dataTables_paginate .paginate_button.next:focus,
.dataTables_wrapper .dataTables_paginate .paginate_button.previous:hover,
.dataTables_wrapper .dataTables_paginate .paginate_button.previous:focus,
.dataTables_wrapper .dataTables_paginate .paginate_button.first:hover,
.dataTables_wrapper .dataTables_paginate .paginate_button.first:focus,
.dataTables_wrapper .dataTables_paginate .paginate_button.last:hover,
.dataTables_wrapper .dataTables_paginate .paginate_button.last:focus {
